const handleSelectCategoryEspSub = async (selectedValues, fieldName) => {
 // console.log(`ðŸ”¹ Procesando selecciÃ³n para: ${fieldName}`, selectedValues);

  // Para todos los campos excepto "autores", usamos la versiÃ³n simple
  if (fieldName !== "autores") {
    let value = Array.from(selectedValues, (option) => option.value ?? option.id);
    setVideo({ ...video, [fieldName]: value });
    return;
  }

  // **Manejo especial para autores**
  try {
    const autoresNoRegistrados = [];
    const autoresConId = new Map();

    const autoresActuales = video.autores || [];

    // Buscar los autores en la base de datos
    const responses = await Promise.allSettled(
      selectedValues.map(async (autor) => {
        try {
          const res = await AutorServer.getAutor(autor.label ?? autor.autor);
          if (res && res.id) {
            autoresConId.set(autor.label ?? autor.autor, res.id);
          }
          return res;
        } catch (error) {
          return null; // Si no existe, devuelve null
        }
      })
    );

    // Actualizar `selectedValues` con los IDs encontrados
    selectedValues = selectedValues.map((autor) => {
      const idExistente = autoresConId.get(autor.label ?? autor.autor);
      return idExistente ? { ...autor, value: idExistente } : autor;
    });

    // Determinar quÃ© autores no estÃ¡n registrados
    responses.forEach((response, index) => {
      const autor = selectedValues[index];
      if (!response.value && !autoresActuales.includes(autor.value ?? autor.id)) {
        autoresNoRegistrados.push(autor);
      }
    });

    // Registrar autores nuevos si es necesario
    if (autoresNoRegistrados.length > 0) {
      const nuevosAutores = await Promise.all(
        autoresNoRegistrados.map(async (autor) => {
          try {
            const formData = new FormData();
            formData.append("autor", autor.label ?? autor.autor);

            const respuestaAutorRegistrado = await AutorServer.RegisterAutor(formData);
            if (respuestaAutorRegistrado && respuestaAutorRegistrado.id) {
              //console.log(`âœ… Autor registrado: ${autor.label ?? autor.autor} con ID ${respuestaAutorRegistrado.id}`);
              return respuestaAutorRegistrado;
            }
          } catch (registerError) {
            console.error(`âŒ Error al registrar autor: ${autor.label ?? autor.autor}`, registerError);
          }
          return null;
        })
      );

      // Actualizar `autoresConId` con los nuevos IDs
      nuevosAutores.forEach((autor) => {
        if (autor && autor.id) {
          autoresConId.set(autor.autor, autor.id);
        }
      });

      // Actualizar `selectedValues` con los nuevos IDs
      selectedValues = selectedValues.map((autorSeleccionado) => {
        const idExistente = autoresConId.get(autorSeleccionado.label ?? autorSeleccionado.autor);
        return idExistente ? { ...autorSeleccionado, value: idExistente } : autorSeleccionado;
      });
    }

    // Obtener IDs finales
    const value = selectedValues.map(option => option.value ?? option.id);
    //console.log("âœ… Autores final seleccionados con ID:", value);

    // Actualizar el estado
    setVideo({ ...video, [fieldName]: value });

  } catch (error) {
    console.error("âŒ Error general al procesar autores:", error);
  }
};





